<template>
<div class="myBody">
    <div class="bg">
        <img v-bind:src='imgUrl' alt=""></div>
    <div class="content">
        <div class="title">
            <h2>支 部 选 择</h2>
            <br>
        </div>
        <div class="depatementTable">
            <el-table :data="tableData" height="100%" border style="width: 100%" @row-click="choose"  highlight-current-row>
                <el-table-column prop="PartyBranchID" label="支部编号"  align="center">
                </el-table-column>
                <el-table-column prop="PartyBranchName" label="支部名称"  align="center">
                </el-table-column>
                <el-table-column prop="City" label="支部所在省"  align="center" >
                </el-table-column>
               
            </el-table>
        </div>

        <div class="bottom-button">
            <div>
                <button @click="next('choicePeople')" class="button-1">确定</button>
            </div>
            <div>
                <router-link to="/" class="button-1">返回</router-link>
            </div>

        </div>

    </div>
<div class="dia_ques" v-if="bool_diaOne">
			<quesDia_one :messageOne="messageOne" v-if="messageOne" @determine="closePopOne"></quesDia_one>
		</div>
</div>
</template>

<script>
	import quesDia_one from "./quesDia_one";
export default {
	components: {
			quesDia_one
		},
	
  name: "choiceDepartment",
  methods: {
  	
  	showPopOne(messsage){
  		this.bool_diaOne=true;
  		this.messageOne=messsage;
  	},
  	closePopOne(){
  		this.bool_diaOne=false;
  	},
  	
    choose(row, column, cell) {
      this.chooseId = row.PartyBranchID;
    },
    next(url) {
      if (this.chooseId != -1) {
        this.$router.push({
          name: url,
          params: {
            PartyBranchID: this.chooseId
          }
        });
      } else {
        this.showPopOne(this.messageA);//请先选择支部！
      }
    },
    listUpdate(list) {
      this.tableData = list;
    },
    closeDoor() {}
  },
  created: function() {
  	  this.$ajax({
      method: "get",
      url: "http://202.206.249.16:8080/dxpj/hp/get_backBround?PictureTypeID=02&AssessTypeID=02"
    })
      .then(res => {
        //info
     
        res = res.data
        console.log(res.PictureTypeName)
       this.imgUrl=res.PictureTypeName
      //  this.imgUrl="../../static/assets/bg_1.jpg"
        
      })
      .catch(function(err) {
        console.log(err)
      });
      
      
    this.$ajax({
      method: "get",
      url: "http://202.206.249.16:8080/dxpj/hp/get_party_selected"
    })
      .then(res => {
        //info

        res = res.data;

        if (res.status.code == 100) {
          var list = res.info;
        } else if (res.status.code == 103) {
          var list = [
            {
              PartyBranchID: "-1",
              PartyBranchName: "暂无",
              City: "暂无"
            }
          ];
        }

        this.listUpdate(list);
      })
      .catch(function(err) {
        console.log(err);
      });
  },
  data() {
    return {
    	bool_diaOne:false,
      messageOne:'',
    	
    	 messageA:"请先选择支部！",
    	
      imgUrl:"../../static/assets/bg_2.jpg",
      chooseId: -1,
      tableData: [
        {
          PartyBranchID: "1",
          PartyBranchName: "test1",
          City: "北京"
        },
        {
          PartyBranchID: "2",
          PartyBranchName: "test1",
          City: "秦皇岛"
        },
        {
          PartyBranchID: "3",
          PartyBranchName: "test1",
          City: "河北"
        },
        {
          PartyBranchID: "4",
          PartyBranchName: "test1",
          City: "安徽"
        },
        {
          PartyBranchID: "5",
          PartyBranchName: "test1",
          City: "上海"
        }
      ]
    };
  }
};
</script>

<style scoped>
	.dia_ques {
	width:100%;
    height:100%;
    background:rgba(0, 0, 0, 0.5);
    position: fixed;
    top:50%;
    left:50%;
    transform:translateX(-50%) translateY(-50%);
    z-index: 99;
	}
	
	
.bottom-button {
  top: 75% !important;
}
.title {
  font-size: 0.4rem;
}
.depatementTable {
  margin: auto;
  width: 70%;
  height: 60%;
  overflow: hidden !important;
  text-align: center;
}
</style>



